aside.render-statistics {
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.3s;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  width: var(--aside-width);
  height: calc(100vh - var(--header-height));
  padding: 1rem;
  background-color: #f5f5f5;
  border-radius: 0.5rem 0 0 0.5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);
}

.close-button-icon {
  position: absolute;
  transition: 0.3s;
  transition-timing-function: ease-in-out;
}

.scrollable-content {
  width: 100%;
  overflow-y: auto;
  padding: 1rem;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
}

.toggle-button {
  position: absolute;
  top: calc(50% - 1rem);
  right: 0.5rem;
  color: #495057;
  font-size: 1.5rem;
  cursor: pointer;
  transition: 0.3s;
}

.close-button {
  left: 0.5rem;
}

.open-button {
  left: -2rem;
}
